import { Component, OnInit, Input } from "@angular/core";
import {
  ModalController,
  NavParams,
  ActionSheetController,
} from "@ionic/angular";
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl,
} from "@angular/forms";
import { UserData } from "../../../../providers/user-data";
import { ToastController } from "@ionic/angular";
import { Camera, CameraOptions } from "@ionic-native/camera/ngx";
import { environment } from "../../../../../environments/environment";
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer/ngx';


@Component({
  selector: "modal-editProfile",
  styleUrls:['./editProfile.scss'],
  templateUrl: "./editProfile.html",
})
export class EditProfilePage {

  @Input() userData: any;
  editProfileForm: FormGroup;

  constructor(
    private modalController: ModalController,
    private _UserData: UserData,
    private toastController: ToastController,
    private camera: Camera,
    public actionSheetController: ActionSheetController,
    private transfer: FileTransfer,
  ) {
    window.addEventListener('ionKeyboardDidShow', ev => {
      document.getElementById("edit-form").style.marginBottom = '250px'
    });

    window.addEventListener('ionKeyboardDidHide', () => {
      document.getElementById("edit-form").style.marginBottom = '0px'
    });
  }

  public baseUrl: any = environment.baseUrl + "/";

  public searchproductList: any = [];
  ngOnInit() {
    this.editProfileForm = new FormGroup({
      fullName: new FormControl(""),
      // city: new FormControl(""),
      mobile: new FormControl(""),
      email: new FormControl(""),
      city: new FormControl(""),
      dob: new FormControl(""),
      gender: new FormControl("Male"),

    });

    console.log(this.userData);
    // this.userData.gender = 'male'
    this.editProfileForm.patchValue(this.userData);
  }

  submit($event) {
    console.log($event);
    let payload = Object.assign({}, $event.value);
    console.log(payload, "payload");
    payload.CustomerImage = this.images.length > 0 ? this.images[0] : this.userData.CustomerImage;
    console.log(payload, "payload");
    this._UserData
      .updateProfile(this.userData._id, payload)
      .subscribe(async (data: any) => {
        console.log("data", data);
        const toast = await this.toastController.create({
          message: "You Successfully Changed you Profile",
           duration: 3000,
                color:'secondary',
          position: "bottom",
          animated: true,
        });
        toast.present();
        this.closeModal();
      });
  }

  async closeModal() {
    const onClosedData: string = "Address Added";
    await this.modalController.dismiss(onClosedData);
  }

  public images: any = [];


  async selectImagefromMobille() {
    const actionSheet = await this.actionSheetController.create({
      header: "Select Image source",
      buttons: [
        {
          text: "Load from Library",
          handler: () => {
            this.pickImage(this.camera.PictureSourceType.PHOTOLIBRARY);
          },
        },
        {
          text: "Use Camera",
          handler: () => {
            this.pickImage(this.camera.PictureSourceType.CAMERA);
          },
        },
        {
          text: "Cancel",
          role: "cancel",
        },
      ],
    });
    await actionSheet.present();
  }

  public myphoto
  pickImage(sourceType) {
    const options: CameraOptions = {
      quality: 100,
      sourceType: sourceType,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      correctOrientation: true,
    };

    this.camera.getPicture(options).then(
      (imageData) => {
        this.myphoto = 'data:image/jpeg;base64,' + imageData;
         this.uploadImage()
            },
          (err) => {
            console.log("err: ", err);
          }
        );

  }

  uploadImage(){
    const fileTransfer: FileTransferObject = this.transfer.create();
    var random = Math.floor(Math.random() * 100);
    let options: FileUploadOptions = {
      fileKey: 'photo',
      fileName: "myImage_" + random + ".jpg",
      chunkedMode: false,
      httpMethod: 'post',
      mimeType: "image/jpeg",
      headers: {
        __authorization_x_token: localStorage.getItem("AuthToken") || "",
      }
    }

    let result ;
    // alert(this.myphoto + " this.myphoto ,,,,, ==")
    fileTransfer.upload(this.myphoto, `${this.baseUrl}api/v1/Admin/saveAllImages`, options)
      .then((data) => {
        result = data
        // alert("data "+JSON.stringify(result)+  JSON.parse(result.response).object)
          this.images[0]= JSON.parse(result.response).object.s3Url
      }, (err) => {
        console.error(err);
        alert("Error ----------------->"+err + JSON.stringify(err));
        // loader.dismiss();
      });
   }

   ngOnDestroy(): void {
     //Called once, before the instance is destroyed.
     //Add 'implements OnDestroy' to the class.
     window.removeEventListener('ionKeyboardDidShow', () => {
    });
   }

}
