import { AfterViewInit, Component } from "@angular/core";
import { Router } from "@angular/router";

import { AlertController } from "@ionic/angular";
import { EditProfilePage } from "./modal/edit-profile/editProfile";
import { UserData } from "../../providers/user-data";
import { ModalController } from "@ionic/angular";
import { ProductService } from "../../providers/product.services";
import { environment } from "../../../environments/environment";

@Component({
  selector: "page-account",
  templateUrl: "account.html",
  styleUrls: ["./account.scss"],
})
export class AccountPage implements AfterViewInit {
  username: string;
  searchWord: any;
  public baseUrl = environment.baseUrl + "/";
  public isDataLoaded: Boolean = false;
  public userData: any = {
    _id: "",
    email: "",
    mobile: "",
    fullName: "Guest",
    dob: "",
    city: "",
    gender: "",
  };

  constructor(
    public alertCtrl: AlertController,
    public router: Router,
    private _UserData: UserData,
    private _ProductService: ProductService,
    public modalController: ModalController
  ) {}

  ngAfterViewInit() {}

  public isHeaderShow: Boolean = false;
  public cartCount = 0

  onScroll(data) {
    // console.log(data.detail.scrollTop,"scrollTop")
    let scrollPoint = data.detail.scrollTop || 0;
    if (scrollPoint > 30) {
      this.isHeaderShow = true;
    }

    if (scrollPoint < 30) {
      this.isHeaderShow = false;
    }
  }

  getCartCount(){
    let shopId = localStorage.getItem('shopID')
    var arr = [];
    this._ProductService
      .cartList(localStorage.getItem("userId"),shopId)
      .subscribe((data: any) => {
        if (data.length !== 0) {
          this.cartCount = data[0].productDetails.length;
         }
      })
  }

  public searchproductList: any = [];
  ngOnInit() {
    this.getCustomerProfile();
    this.getCartCount();
  }

  public showSearchBox: Boolean = false;
  enableSearch() {
    this.showSearchBox = true;
  }

  backToNormal() {
    this.showSearchBox = false;
  }

  getCustomerProfile() {
    this._UserData
      .getCustomerDetails(localStorage.getItem("userId"))
      .subscribe((data: any) => {
        this.userData = data;
        this.isDataLoaded = true;

         ( this.userData.hasOwnProperty('fullName'))? '' : this.userData = "Guest"
        console.log(this.userData, "---");
      });
  }

  updatePicture() {
    console.log("Clicked to update picture");
  }

  async openEditProfileModal() {
    console.log(this.userData, "---inside");
    const modal = await this.modalController.create({
      component: EditProfilePage,
      componentProps: {
        userData: this.userData,
      },
    });

    modal.onDidDismiss().then((dataReturned) => {
      this.getCustomerProfile();
    });

    return await modal.present();
  }


}
